.container-about {
    margin-top: 1rem;
    height: 100%;
    background: url('http://curly.mikado-themes.com/wp-content/uploads/2018/04/p-parallax-img-1.jpg');
    background-attachment: fixed;
    background-position: center top !important;
    background-size: cover;
    -webkit-box-shadow: inset 0px 43px 50px -5px rgba(255,255,255,1);
            box-shadow: inset 0px 43px 50px -5px rgba(255,255,255,1);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;

    .about-content {
        width: 40rem;
        height: 100%;
       background: rgba(black, 0.3);
       text-align: center;
       -webkit-box-shadow: 0px 0px 40px -5px rgba(0,0,0,0.75);
               box-shadow: 0px 0px 40px -5px rgba(0,0,0,0.75);
       margin: 5rem 3rem;
       -webkit-animation: slideInAbout 0.9s ease forwards;
               animation: slideInAbout 0.9s ease forwards;

       h1 {
           letter-spacing: 0.25em;
           padding-top: 1rem;
           -webkit-animation: slideInAbout 1.6s ease-in forwards;
                   animation: slideInAbout 1.6s ease-in forwards;
       }
       p {
           text-align: left;
           width: 100%;
           padding: 2rem 2rem 0 2rem;
           font-weight: bold;
           -webkit-animation: scaleAnimation 2s ease-in forwards;
                   animation: scaleAnimation 2s ease-in forwards;
           img {
            float: right;
            width: 13rem;
            height: 13rem;
            margin: 0.5rem;
           }
       }
       .secondP {
           padding: 2rem;
       }
    }

}


