.loginbar {
    background: black;
    height: 2.5rem;
    -webkit-box-shadow: 0px 6px 21px 0px rgba(0,0,0,0.75);
            box-shadow: 0px 6px 21px 0px rgba(0,0,0,0.75);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.nav-toggle i {
	color: #C79438;
	padding-right: 1rem;
	font-size: 2em;
}

.nav-toggle {
	display: none;
}

.fa-phone-volume {
    color: #C79438;
    font-size: 1.5em;
    padding: 0.35rem;
    padding-left: 5rem;
}

.loginbar span {
    color: grey;
    font-size: 0.9em;
	padding-left: 0.5rem;
	font-family: 'Poiret One', cursive;
}

.loginbar span:hover {
    color: #C79438;
}

body > div > header > div > div {
    padding-right: 5rem;
}

a.animated-button:link, a.animated-button:visited {
	position: relative;
	display: block;
    margin: 0.1rem auto;
    padding: 0.3rem 2rem;
	color: #C79438;
	font-size:12px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	overflow: hidden;
	letter-spacing: .08em;
	border-radius: 0;
	text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
   z-index: 3;
}
a.animated-button:link:after, a.animated-button:visited:after {
	content: "";
	position: absolute;
	height: 0%;
	left: 50%;
	top: 50%;
	width: 150%;
	z-index: -1;
	-webkit-transition: all 0.75s ease 0s;
	-o-transition: all 0.75s ease 0s;
	transition: all 0.75s ease 0s;
}

a.animated-button:link:hover, a.animated-button:visited:hover {
	color: black;
	text-shadow: none;
}

a.animated-button:link:hover:after, a.animated-button:visited:hover:after {
	height: 450%;
}

a.animated-button:link, a.animated-button:visited {
	position: relative;
	display: block;
    margin: 0.1rem auto;
    padding: 0.3rem 2rem;
	color: #C79438;
	font-size:14px;
	border-radius: 0;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	overflow: hidden;
	letter-spacing: .08em;
	text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
  
}

a.animated-button.log-in {
    border: 2px solid #C79438;

}
a.animated-button.log-in::after {
	background: #C79438;
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(25deg);
        -ms-transform: translateX(-50%) translateY(-50%) rotate(25deg);
            transform: translateX(-50%) translateY(-50%) rotate(25deg);

}

