#contact {
    width: 100%;
    height: 100vh;
   background: url('https://martinvalchevv.github.io/MWhairstyles/img/map-bg.jpg') no-repeat;
    overflow: hidden;
    background-position: center;
    background-size: cover;
    padding-bottom: 200px;
   
  }

  .all-info {
      background: rgba($color: #111, $alpha: 0.8);
      height: 100vh;
  }

  .section-header {
    text-align: center;
    margin: 0 auto;
    padding-top: 50px;
    margin-bottom: 50px;
    letter-spacing: 6px;
    color: #C79438; 
    margin-bottom: 25px;
  }
  
  .contact-wrapper {
    margin: 0 auto;
    padding-top: 20px;
    position: relative;
    max-width: 800px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  
  .form-horizontal {
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-pack: justify;
         -ms-flex-pack: justify;
             justify-content: space-between;
     -webkit-box-orient: vertical;
     -webkit-box-direction: normal;
         -ms-flex-direction: column;
             flex-direction: column;
     width: 50%;
     margin-right: 4rem;
  }

  .form-control, textarea {
    width: 100%;
    background-color:transparent;
    color: white;
    letter-spacing: 1px;
    font-weight: bold;
    margin-bottom: 1rem;
    outline: none;
    border: 2px solid #C79438;
    padding-left: 0.4rem;
    border-radius: 0.3rem;
  }

  .form-control:focus::-webkit-input-placeholder, textarea:focus::-webkit-input-placeholder {
      color: transparent;
  }

  .form-control:focus:-ms-input-placeholder, textarea:focus:-ms-input-placeholder {
      color: transparent;
  }

  .form-control:focus::-ms-input-placeholder, textarea:focus::-ms-input-placeholder {
      color: transparent;
  }

  .form-control:focus::placeholder, textarea:focus::placeholder {
      color: transparent;
  }

  .send-button {
    height: 2rem;
    width: 100%;
    overflow: hidden;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    font-size: 16px;
    background: #C79438;
    border: none;
    cursor: pointer;
  }

  .fa-paper-plane {
      margin-top: 0.4rem;
  }
  
  .btn-send {
    width: 100%;
    height: 2rem;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .send-text {
    display: block;
    margin-top: 0.5rem;
    letter-spacing: 2px;
    font-weight: bold;
  }
  
  .btn-send:hover {
    -webkit-transform: translate3d(0, -1.6rem, 0);
            transform: translate3d(0, -1.6rem, 0);
  }

 #name:focus, #email:hover, #txt:focus {
    border: 2px solid #C79438;
 }



 .direct-contact-container {
     display: block;
     width: 50%;
     background: transparent;

     hr {
        border-color: rgba(#C79438, 0.8); 
      }

     ul {
         list-style: none;
         padding: 0 0 2rem 0;
         i {
             color: #C79438;
             padding-right: 1rem;
         }

         li {
             padding-bottom: 1rem;
         }
        a {
            text-decoration: none;
            color: white;
        }

        .gmail {
            font-size: 1em;
        }

     }

     .social-media-list {
         display: -webkit-box;
         display: -ms-flexbox;
         display: flex;
         -webkit-box-pack: center;
             -ms-flex-pack: center;
                 justify-content: center;
         -webkit-box-align: center;
             -ms-flex-align: center;
                 align-items: center;
         height: 8rem;
         padding: 0;
         font-size: 2rem;

         li {
             padding: 0;
             margin: 1rem;
             background: transparent;
             border-radius: 50%;
             -webkit-transition: all .2s ease-in-out;
             -o-transition: all .2s ease-in-out;
             transition: all .2s ease-in-out;
             
         }

         li:hover {
            background-color: #C79438; 
         }

         i {
            color: #C79438;
             padding: 1rem;;
         }

         i:hover {
            color: black;
         }
     }
 }